.ocm-upgrade-graph{border-width:1px;max-width:800px}@media (max-width: 400px){.ocm-upgrade-graph{display:none;flex-wrap:wrap}}.ocm-upgrade-graph-container{align-items:center;display:flex;font-size:var(--pf-global--FontSize--sm)}.ocm-upgrade-graph-path{display:flex;list-style:none;margin:0;padding:0;position:relative;width:100%;margin-top:20px}.ocm-upgrade-graph-line{align-items:center;display:flex;height:60px;justify-content:center;position:relative;min-width:50px;width:100%}.ocm-upgrade-graph-line::before{background-color:#06c;content:'';height:4px;position:absolute;width:100%}.ocm-upgrade-graph-line:last-child::after{background:transparent;border:8px solid transparent;border-left-color:var(--pf-global--BorderColor--100);border-width:8px 12px;content:'';position:absolute;right:-15px;border-left-color:var(--pf-global--primary-color--100)}.ocm-upgrade-graph-version{color:initial;display:flex;flex-direction:column;height:35px;justify-content:flex-end;line-height:1;position:absolute;text-align:center;top:-20px;font-weight:var(--pf-global--FontWeight--bold)}.ocm-upgrade-graph-version-dot{background:var(--pf-global--primary-color--100);border-radius:16px;content:'';height:16px;position:absolute;width:16px;z-index:2}.ocm-upgrade-graph-version-dot.ocm-upgrade-current::after{background:transparent !important}.ocm-upgrade-graph-version-dot::after{background:#fff;border:2px solid #fff;border-radius:12px;content:'';height:12px;left:2px;position:absolute;top:2px;width:12px}.ocm-upgrade-graph-more-versions{border:1px solid;border-radius:15px;border-color:#06c;color:#06c;background:white;display:inline-block;font-size:var(--pf-global--FontSize--xs);justify-content:center;padding:0 8px;position:absolute}.ocm-upgrade-graph-more-versions::after{display:none}
