/* pf-u-float-right does not work in all flows */
.facet-lib-float-right {
  float: right !important;
}

.cluster-toolbar {
  padding: 0 !important;
}
.cluster-toolbar__content {
  padding: 0 !important;
}
.cluster-toolbar-ocm {
  margin-right: 50px; /* fixed size of "a bulb" in OCM */
}
.table-no-padding {
  --pf-c-table-cell--PaddingLeft: 0 !important;
}

#checkbox-deselect-all-hosts-action {
  visibility: hidden;
}

.pf-c-alert-group.alerts-section {
  position: absolute;
  padding: var(--pf-c-page__main-section--PaddingTop);
  bottom: 0;
  width: 100%;
}

.ssh-public-key__textarea {
  margin-top: var(--pf-c-form__group-label--PaddingBottom);
}

.detail-list {
  display: block !important;
}

.detail-list dd {
  margin-bottom: var(--pf-global--spacer--sm);
}
.host-validation-groups__validation-group {
  margin-top: var(--pf-global--spacer--md) !important;
  margin-bottom: var(--pf-global--spacer--sm) !important;
}

pre.prism-code {
  padding: var(--pf-global--spacer--sm) var(--pf-global--spacer--md);
  overflow-x: auto;
}

.host-table-alerts {
  padding-top: var(--pf-global--spacer--lg) !important;
}

.hosts-table .pf-c-dropdown__toggle-text {
  color: var(--pf-global--Color--100) !important;
}

.hosts-table button {
  font-size: var(--pf-c-table-cell--FontSize);
}

.hosts-table {
  height: 1rem; /* Workaround for: https://bugzilla.redhat.com/show_bug.cgi?id=1879378 in FF; The actual table height will be recalculated accordingly. */
}

.hosts-table-sublabel {
  color: var(--pf-global--palette--black-600);
}

.cluster-validation-section {
  padding-bottom: var(--pf-global--spacer--md) !important;
}

.cluster-progress-bar {
  margin-top: -2rem; /* Progress.title property is string-only, we need to stack components as a workaround */
}

.form-new-cluster .form-group-pull-secret {
  height: calc(100vh - 650px);
}

.form-new-cluster .form-group-pull-secret .pf-c-form__group-control {
  height: 100%;
}

.form-new-cluster .form-group-pull-secret .pf-c-form__group-control textarea {
  height: 100%;
}
